import hlogo from "../assets/img/b-logo.png";
import React, { useState } from "react";
import axios from "axios";
const RegisterForm = ({setScreen}) => {
  const iFormDAta = {
    role: "",
    username: "",
    name: "",
    password: "",
    email: "",
  };
  const [formData, setFormData] = useState(iFormDAta);
  const [inNextStep, setInNextStep] = useState(false);
  const register = () => {
    axios
      .post("/api/user/register", formData)
      .then(({ data }) => {
        if ((data.status = "amjilttai")) {
          setFormData(iFormDAta);
          setInNextStep(true);
        }
      })
      .catch((e) => {
        alert("please check your internet connection");
      });
  };
  const nextStep = () => {
    setScreen("login")
  };
  return (
    <div className="content">
      <div className="c-login c-register">
        <img src={hlogo} alt="logo" />
        {inNextStep ? null : <p className="title">Бүртгүүлэх</p>}
        {inNextStep ? (
          <Journalist nextStep={nextStep}/>
        ) : (
          <form
            onSubmit={(e) => {
              e.preventDefault();
              register();
            }}
            autoComplete="off"
          >
            <input
              type="text"
              placeholder="Нэвтрэх нэр"
              name="username"
              value={formData.username}
              onChange={({ target }) =>
                setFormData({ ...formData, [target.name]: target.value })
              }
            />
            <input
              type="text"
              placeholder="E-mail"
              name="email"
              value={formData.email}
              onChange={({ target }) =>
                setFormData({ ...formData, [target.name]: target.value })
              }
            />

            <input
              type="password"
              placeholder="Нууц үг"
              name="password"
              value={formData.password}
              onChange={({ target }) =>
                setFormData({ ...formData, [target.name]: target.value })
              }
            />
            <select
              name="role"
              value={formData.role}
              onChange={({ target }) =>
                setFormData({ ...formData, [target.name]: target.value })
              }
            >
              <option value="">-</option>
              <option value="journalist">Сэтгүүлч</option>
              <option value="checker">Баримт шалгагч</option>
              <option value="source">Эх сурвалж</option>
            </select>
            <div className="c-button">
              <div
                className="button"
                onClick={(e) => {
                  e.preventDefault();
                  register();
                }}
              >
                Үргэлжлүүлэх
              </div>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

const Journalist = ({nextStep}) => {
  return (
    <div
      style={{
        display: "flex",
        marginTop: "25px",
        textAlign: "center",
        width: "80%",
        alignSelf: "center",
        flexDirection: "column",
      }}
    >
      <p
        style={{
          fontFamily: "Montserrat",
          fontWeight: "normal",
          fontSize: "27px",
          textAlign: "center",
          color: "#fff",
        }}
      >
        Бүртгэл амжилттай хийгдлээ.
        <br />
        Танд баярлалаа
      </p>
      <div
        className="c-button"
        style={{ marginTop: "60px", marginBottom: "40px" }}
      >
        <div
          className="button"
          onClick={(e) => {
            e.preventDefault();
            nextStep();
          }}
        >
          Үргэлжлүүлэх
        </div>
      </div>
    </div>
  );
};

export default RegisterForm;
